<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="backgroundNaviBar headerCont header">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">污染上传</span>
    </mu-appbar> -->
     <van-nav-bar
        :fixed="true"
        title="废液管理"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="plus" slot="right" size="20" /> -->
      </van-nav-bar>
    <div class="containerContent">
      <div class="containerBack">
        <div class="flexContainer">
          <div class="Title">
            <span>水站名称</span>
          </div>
          <div class="imageSize" id="selectStation" @click="selectDataPic">
            <input type="text" class="input" placeholder="选择水站名称" v-model="station" disabled />
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
        <div class="flexContainer">
          <div class="Title">
            <span>上传时间</span>
          </div>
          <div class="imageSize" @click="selectTime">
            <input type="text" class="input" placeholder="选择上传时间" v-model="uploadTime" disabled />
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
        <div class="flexContainer">
          <div class="Title">
            <span>超标因子</span>
          </div>
          <div class="imageSize" @click="()=>{show=true}">
            <input type="text" class="input" placeholder="选择超标因子" v-model="factor" disabled />
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
        <div class="flexContainer">
          <div class="Title">
            <span>上传人员</span>
          </div>
          <div class="imageSize" @click="selectDataPersonPic" id="selectPerson">
            <input type="text" class="input" placeholder="选择上传人员" v-model="person" disabled />
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
        <div class="flexContainer">
          <div class="Title">
            <span>超标企业</span>
          </div>
          <div class="imageSize" id="selectEnterprise" @click="selectDataEnterprisePic">
            <input type="text" class="input" placeholder="选择超标企业" v-model="enterprise" disabled />
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="containerBack">
        <div class="contTextArea">
          <div>
            <span>超标原因描述</span>
          </div>
          <div>
            <textarea name id placeholder="请输入超标原因" class="TextArea" v-model="reason"></textarea>
          </div>
        </div>
      </div>
      <div class="containerBack">
        <div class="contTextArea">
          <div>
            <span>处理结果描述</span>
          </div>
          <div>
            <textarea name id placeholder="请输入处理结果描述" class="TextArea" v-model="description"></textarea>
          </div>
        </div>
      </div>
      <div class="containerBack">
        <div class="UploadImage">
          <van-uploader v-model="fileList" multiple :after-read="afterRead" />
        </div>
      </div>
      <div class="containerBack">
        <div class="contTextArea">
          <div>
            <span>备注</span>
          </div>
          <div>
            <textarea name id placeholder="请输入其他相关描述" v-model="remark" class="TextArea"></textarea>
          </div>
        </div>
      </div>
      <div class="submit">
        <!-- .backgroundNaviBar { -->
        <div class="BtnSubmit backgroundNaviBar" @click="submit">
          <span>上传</span>
        </div>
      </div>
    </div>
    <van-action-sheet v-model="show" title="选择因子">
      <van-checkbox-group v-model="result" class="flexCheckbox" @change="FactorChange">
        <div v-for="(item,index) in $store.state.stationContent" :key="index" class="checkbox">
          <van-checkbox class="checkboxChild" :name="item">{{item}}</van-checkbox>
        </div>
      </van-checkbox-group>
    </van-action-sheet>

    <van-action-sheet v-model="showTime" title="选择上传日期">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirmDateTime"
        @cancel="close"
      />
    </van-action-sheet>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Uploader } from "vant";

Vue.use(Uploader);
import { Toast } from "vant";
export default {
  data() {
    return {
      fileList: [
        // { url: "https://img.yzcdn.cn/vant/leaf.jpg" }
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
        // { url: "https://cloud-image", isImage: true }
      ],
      file: null,
      description: "",
      remark: "",
      enterprise: "",
      reason: "",
      token: "",
      factor: "",
      person: "",
      show: false,
      minHour: 10,
      maxHour: 20,
      minDate: new Date(1970, 7, 1),
      maxDate: new Date(2029, 10, 1),
      currentDate: new Date(),
      result: [],
      showTime: false,
      uploadTime: "",
      station: "",
      formdata: new FormData()
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
    console.log(this.$store.state);
  },
  methods: {
    FactorChange(value) {
      //   console.log(value);
      console.log(value);
      var result = "";
      for (let index = 0; index < value.length; index++) {
        result += value[index] + "、";
      }
      this.factor = result;
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      console.log(file);
      this.file = file;
      this.formdata.append("files", file.file);
    },
    async submit() {
      Toast.loading({
        message: "数据上传中...",
        forbidClick: true,
        loadingType: "spinner"
      });
      var url = "/pollutionTrace";
      var _this = this;
      //   var params = {
      //     description: _this.description,
      //     enterprise: _this.enterprise,
      //     factor: _this.factor,
      //     files: _this.file,
      //     gmtCreate: _this.uploadTime,
      //     name: "",
      //     person: _this.person,
      //     reason: _this.reason,
      //     remark: _this.remark,
      //     station: _this.station
      //   };
      this.formdata.append("description", _this.description);
      this.formdata.append("enterprise", _this.enterprise);
      this.formdata.append("factor", _this.factor);
      this.formdata.append("gmtCreate", _this.uploadTime);
      this.formdata.append("person", _this.person);
      this.formdata.append("reason", _this.reason);
      this.formdata.append("remark", _this.remark);
      this.formdata.append("station", _this.station);
      var res = await AppBace.request(url, "POST", _this.formdata, _this.token);
      console.log(res);
      if (res.data.message === "success") {
        this.$router.go(-1);
        Toast.success("数据上传成功");
      } else {
        Toast.fail("上传失败,请重试");
        //     Toast.loading({
        //     message: "数据上传中...",
        //     forbidClick: true,
        //     loadingType: "spinner"
        //   });
      }
    },
    // 选择站点
    selectDataPic() {
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectStation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.station = selectArr[0];
        }
      });
    },
    // 企业
    selectDataEnterprisePic() {
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectEnterprise, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.enterprise, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择企业", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.enterprise = selectArr[0];
        }
      });
    },
    // 上报人员
    selectDataPersonPic() {
      var _this = this;
      var arr = [];
      var pickerView = new PickerView({
        bindElem: selectPerson, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择上传人员", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.person = selectArr[0];
        }
      });
    },
    // 因子
    selectDataFactorPic() {},
    // 上传时间
    selectTime() {
      this.showTime = true;
    },
    confirmDateTime(value) {
      this.uploadTime = AppBace.filter(value);
      this.showTime = false;
    },
    close() {
      this.showTime = false;
    }
  }
};
</script>
<style scoped>
.imageSize img {
  width: 16px;
  height: 16px;
}
.imageSize {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 30px;
  justify-content: space-between;
  flex: 4;
}
.input {
  border: none;
  flex: 1;
  padding: 1px 5px;
  background: white;
}
.flexContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  /* background: white; */
}
.Title {
  flex: 1;
}
.containerBack {
  /* padding: 10px; */
  border-radius: 10px;
  background: white;
  margin-bottom: 10px;
}
.containerContent {
  padding: 10px 5px;
  margin-top: 46px;
}
.TextArea {
  width: 100%;
  height: 80px;
  border-radius: 10px;
  padding: 10px;
}
.contTextArea {
  padding: 10px 20px;
  /* border-radius: 10px; */
}
.UploadImage {
  padding: 10px;
}
.BtnSubmit {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  height: 40px;
  border-radius: 10px;
}
.submit {
  display: flex;
  justify-content: center;
}
.checkbox {
  display: flex;
  flex-direction: row;
  width: 100px;
  height: 40px;
}
.checkboxChild {
  width: 100px;
  display: flex;
  /* justify-content: center; */
}
.flexCheckbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 10px;
  justify-content: space-between;
}
.headerCont {
  position: fixed;
  top: 0;
  width: 100%;
}
</style>


